import { GetList, Delete } from '@/api/faultManagement/analysis';
import { UpdateStatus } from '@/api/equipmentManagement/earlyWarning';
import Detail from "./detail";
import BaseInformation from "./baseInformation";
import { pagging, keyword, buttonPermissions } from '@/mixin';
export default {
  name: 'Analysis',
  components: {
    Detail: Detail,
    BaseInformation: BaseInformation
  },
  mixins: [pagging, keyword, buttonPermissions],
  data: function data() {
    return {
      baseInfoView: false,
      tableData: [],
      detailShow: false,
      toolbeRunning: false,
      // 节流阀,
      str: ''
    };
  },
  mounted: function mounted() {
    this.init();

    if (this.$store.state.user.config.isPro) {
      this.str = '/ExpertSystem/FaultManagement/analysisDetail2';
    } else {
      this.str = '/FaultManagement/analysisDetail2';
    }
  },
  methods: {
    baseInfoHidden: function baseInfoHidden() {
      this.baseInfoView = false;
      this.init();
    },
    init: function init() {
      var _this = this;

      GetList({
        keyword: this.keyword,
        pageIndex: this.pageIndex,
        pageSize: this.pageSize
      }).then(function (res) {
        _this.$set(_this, 'tableData', res.data.items);

        _this.totalCount = res.data.totalCount;
        _this.pageIndex = res.data.pageIndex;
        _this.pageSize = res.data.pageSize;
      });
    },
    insert: function insert() {
      var _this2 = this;

      this.baseInfoView = true;
      this.$nextTick(function () {
        _this2.$refs.baseInfo.init();
      });
    },
    baseInfo: function baseInfo(dataObj) {
      var _this3 = this;

      this.baseInfoView = true;
      this.$nextTick(function () {
        _this3.$refs.baseInfo.init(dataObj);
      });
    },
    edit: function edit(id) {
      var _this4 = this;

      this.detailShow = true;
      this.$nextTick(function () {
        _this4.$refs.detail.init(id);
      });
    },
    editHidden: function editHidden() {
      this.detailShow = false;
      this.init();
    },
    del: function del(id) {
      var _this5 = this;

      this.$confirm('此操作将删除预警规则, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(function () {
        Delete(id).then(function () {
          _this5.$message({
            type: 'success',
            message: '删除成功',
            duration: 500,
            onClose: function onClose() {
              _this5.pageIndex = 1;

              _this5.init();
            }
          });
        });
      }).catch(function () {});
    },
    // 发送更改请求
    changStatus: function changStatus(type, data) {
      var _this6 = this;

      UpdateStatus({
        id: data.id,
        status: type === 'on' ? 100 : 0
      }).then(function () {
        _this6.$message({
          type: 'success',
          message: "\u5DF2\u6210\u529F".concat(type === 'on' ? '启用' : '停用'),
          duration: 500,
          onClose: function onClose() {
            _this6.toolbeRunning = false;

            _this6.init();
          }
        });
      }).catch(function (err) {
        if (err) {
          _this6.toolbeRunning = false;
        }
      });
    }
  }
};