import request from '@/utils/request';
var api = {
  GetList: '/api/dms/Alert/Get',
  Update: '/api/dms/Alert/Update',
  Create: '/api/dms/Alert/Create',
  UpdateStatus: '/api/dms/Alert/UpdateStatus',
  Delete: '/api/dms/Alert/Delete',
  GetById: '/api/dms/Alert/GetById'
};
/**
 * @description 获取预警列表
 * @param  {Object} obj
 */

export function GetList(obj) {
  return request({
    url: api.GetList,
    method: 'get',
    headers: {
      'Content-Type': 'application/json'
    },
    params: obj
  });
}
/**
 * @description 编辑预警
 * @param  {Object} obj
 */

export function Update(obj) {
  return request({
    url: api.Update,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
}
/**
 * @description 创建预警
 * @param  {Object} obj
 */

export function Create(obj) {
  return request({
    url: api.Create,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
}
/**
 * @description 修改预警状态
 * @param  {Object} obj
 */

export function UpdateStatus(obj) {
  return request({
    url: api.UpdateStatus,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: obj
  });
}
/**
 * @description 删除预警
 * @param  {String} id
 */

export function Delete(id) {
  return request({
    url: api.Delete,
    method: 'post',
    headers: {
      'Content-Type': 'application/json'
    },
    data: {
      id: id
    }
  });
}
/**
 * @description 获取预警设置详情
 * @param  {String} id
 */

export function GetById(id) {
  return request({
    url: api.GetById,
    method: 'get',
    headers: {
      'Content-Type': 'application/json'
    },
    params: {
      id: id
    }
  });
}